import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { trigger, state, style, animate, transition } from '@angular/animations';

/**
 * Generated class for the LifestyleCheckStatusPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lifestyle-check-status',
  templateUrl: 'lifestyle-check-status.html',
  animations: [
    trigger('fadeInOut', [
      transition(':enter', [
        style({ opacity: 0 }),
        animate('200ms ease-in', style({ opacity: 1 }))
      ]),
      transition(':leave', [
        animate('200ms ease-out', style({ opacity: 0 }))
      ])
    ]),
    trigger('slideUpDown', [
      transition(':enter', [
        style({ transform: 'translateY(100%)' }),
        animate('250ms ease-out', style({ transform: 'translateY(0)' }))
      ]),
      transition(':leave', [
        animate('250ms ease-in', style({ transform: 'translateY(100%)' }))
      ])
    ])
  ]
})
export class LifestyleCheckStatusPage {
  showConfirm = false;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LifestyleCheckStatusPage');
  }

  closeModal() {
    this.showConfirm = false;
  }

  lanjutkanPembayaran() {
    this.showConfirm = false;
    console.log("Lanjutkan Pembayaran");
  }

  keluarDariPembayaran() {
    this.showConfirm = false;
    console.log("Keluar dari Pembayaran");
  }

}



